const adaptProductMiniature = () => {
  const $miniatures = $('.js-product-miniature');

  Array.from($miniatures).forEach((e) => {
    const $thumbnailContainer = $(e).find('.thumbnail-container');
    const $highlightedInformations = $(e).find('.highlighted-informations');
    const $widget = $(e).find('.ts-wdg-loc-pl-wrapper');

    if ($widget.length > 0) {
      adaptThumbnailContainer(e);

      if (!$thumbnailContainer.find('.thumbnail-top').length) {
        $thumbnailContainer.prepend($('<div class="thumbnail-top"></div>'));
        const $thumbnailTop = $(e).find('.thumbnail-top');
        const $productThumbnail = $(e).find('.product-thumbnail');
        $thumbnailTop.append($productThumbnail);
        $thumbnailTop.append($highlightedInformations);
      }
    }
  });
}

const adaptThumbnailContainer = (element) => {
  setTimeout(() => {
    const $widget = $(element).find('.ts-wdg-loc-pl-wrapper');
    const $thumbnailContainer = $(element).find('.thumbnail-container');
    $thumbnailContainer.addClass('with-ts-widget');

    if ($widget.height() > 65 || $widget.height() == 0) {
      $thumbnailContainer.addClass('with-ts-widget-expanded');
    }
  }, 500);
}

$(() => {
  const parentObjProductDesc = $('.ts-wdg-loc-pd-wrapper');
  const parentObjProductName = $('.ts-wdg-loc-pn-wrapper');
  const parentObjAnchorStars = $('etrusted-product-review-list-widget-product-star-extension');
  const parentObjHeader = $('.ts-wdg-loc-hd-wrapper');
  const mobileTopMenuWrapper = $('.header-top .container');
  
  if ($('#description').length) {
    if (typeof window.controller !== 'undefined') {
      if (window.controller === 'product') {
        parentObjProductDesc.detach().appendTo($('body').find('#description'));
      }
    } else {
      parentObjProductDesc.detach().appendTo($('body').find('#description'));
    }
  }
  
  if (typeof window.controller !== 'undefined') {
    if (window.controller === 'product') {
      parentObjProductName.detach().insertAfter($('body').find('h1'));
      parentObjAnchorStars.detach().insertAfter($('body').find('h1'));
    }
  } else {
    parentObjProductName.detach().insertAfter($('body').find('h1'));
    parentObjAnchorStars.detach().insertAfter($('body').find('h1'));
  }

  if (typeof prestashop !== 'undefined') {
    prestashop.on('updateProductList', () => {
      adaptProductMiniature();
    });
  }

  const moveHeaderWidget = () => {    
      mobileTopMenuWrapper.append(parentObjHeader);
  }

  if (parentObjHeader.length) {
    parentObjHeader.addClass('row');
    moveHeaderWidget();
  }

  $(window).on('resize', () => {
    moveHeaderWidget();
  });
});

$(window).on('load', () => adaptProductMiniature());
